import React from "react";
import { Grid, Typography, Box, Button } from "@material-ui/core";
import AssignmentIcon from "@material-ui/icons/Assignment";

const NuestraMision = () => {
  return (
    <>
      <Grid
        container
        direction="row"
        justifyContent="center"
        alignItems="flex-end"
        style={{ marginTop: 40, marginBottom: 80 }}
      >
        <Typography variant="h2" component="h2" style={{ color: "black" }}>
          Percepción de CiberSeguridad
        </Typography>
      </Grid>
      <Grid
        container
        direction="row"
        justifyContent="space-evenly"
        alignItems="center"
      >
        <div>
          <Grid
            container
            direction="column"
            justifyContent="center"
            alignItems="center"
          >
            <Typography component="div">
              <Box
                sx={{
                  fontStyle: "italic",
                  width: 600,
                  fontSize: 20,
                  color: "black",
                }}
              >
                Somos estudiantes del ITAM que en colaboración con SocialTIC
                buscamos conocer la percepción sobre la ciberseguridad entre
                estudiantes mexicanos de licenciatura. Esto con la finalidad de
                desmentir o comprobar ciertos mitos que existen sobre su
                conocimiento acerca de lo que realmente pasa con su información
                cada que entran al internet.
              </Box>
            </Typography>
          </Grid>
        </div>
        <div>
          <Grid
            container
            direction="column"
            justifyContent="center"
            alignItems="center"
            style={{ marginTop: 70 }}
          >
            <Typography component="div">
              <Box
                sx={{
                  fontStyle: "italic",
                  width: 600,
                  fontSize: 20,
                  color: "black",
                }}
              >
                Con esta encuesta buscamos entender los mitos de ciberseguridad
                que los jóvenes universitarios de entre 18 y 24 años pueden
                llegar a tener. Específicamente en los campos de redes sociales
                y compras en línea.
              </Box>
            </Typography>
          </Grid>
        </div>
      </Grid>
      <Grid
        container
        direction="row"
        justifyContent="center"
        alignItems="center"
        style={{ marginTop: 80 }}
      >
        <Button
          variant="contained"
          size="medium"
          startIcon={<AssignmentIcon />}
        >
          Realiza Esta Encuesta
        </Button>
      </Grid>
    </>
  );
};

export default NuestraMision;
